.Testimonial-box{
    margin: 10px 0;
}
.testi-img{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    border: 2px solid white;
    margin-right: 18px;
    box-shadow: 0px 0px 4px rgb(57, 56, 56);
    /* margin: auto; */
    /* margin-bottom: 10px; */
}
.reviews-box{
    position: relative;
    width: 100%;
    padding: 40px;
    background: white;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.347);
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.reviews-box::before{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-width: 20px 20px 12px 0;
    border-style: solid;
    border-color: transparent #fff transparent #fff;
    z-index: 1;
    left: -20px;
    top: 30px;
}
.testi-words{
    font-size: 17px;
    color: rgb(107, 106, 106);
    line-height: 1.28;
    letter-spacing: 0;
}
.testi-name-box{
    width: 100%;
    position: relative;
    margin-bottom: 15px;
    /* margin-top: 31px; */
    /* text-align: center; */
    display: flex;
    align-items: center;
}
