.testi-name{
    font-size: 22px;
    font-weight: 600;
    color: black;
    line-height: 1;
    margin-bottom: 2px;
}
.testi-position{
    font-size: 12px;
    display: block;
    font-weight: 600;
    color: var(--primry-clr);
    text-transform: uppercase;
}
@media (max-width: 768px) {
    .testi-img{
        width: 50px;
        height: 50px;
        border: 3px solid white;
        margin-right: 30px;
        margin-top: 19px;
    }
    .testi-words{
        font-size: 16px;
    }
    .reviews-box{
        padding: 25px;
        width: calc(100% - 80px);
    }
    .testi-name-box i{
        font-size: 35px;
    }
}